body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:0.8rem}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

#main-banner{height:60px;background-color:#303236}#main-title{font-family:Roboto,-apple-system,BlinkMacSystemFont,"Helvetica Neue","Segoe UI","Oxygen","Ubuntu","Cantarell","Open Sans",sans-serif;font-weight:100;color:#fff}#main-logo{margin:0 10px 10px}#navigation{padding:50px 10px;border-right:1px solid #dedede;height:calc(100vh - 60px);overflow:auto}pre{color:#fff !important;background-color:#2a274a;padding:10px 15px;border-radius:5px}.disable-selection{-moz-user-select:none;-ms-user-select:none;-khtml-user-select:none;-webkit-user-select:none;-webkit-touch-callout:none}#busy-mask{position:absolute;z-index:2;background-color:rgba(144,144,144,0.0392157);height:calc(100% - 60px);top:60px;left:0;width:100%}.node-details{overflow-y:auto;height:calc(100vh - 100px)}.pointer{cursor:pointer}

.tv-collapsed{height:0;overflow:hidden}

.lds-ellipsis{position:relative;width:64px;height:64px;left:50%;-webkit-transform:translate(-50%, 50%);transform:translate(-50%, 50%);top:30%}.lds-ellipsis div{position:absolute;top:27px;width:20px;height:20px;border-radius:50%;background:#3a3a3a;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:10px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:15px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:38px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:60px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(19px, 0);transform:translate(19px, 0)}}@keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(19px, 0);transform:translate(19px, 0)}}@-webkit-keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}

